.contactsPG,
.articlesPG,
.servicesPG,
.reviewsPG,
.articlePG {
	padding-top: 60px;
	@media (min-width: $desktop) {
		padding-top: 80px;
	}
}

.contactsPG,
.reviewsPG {
	background: linear-gradient(
		180deg,
		rgba(241, 218, 215, 0) 0%,
		rgba(241, 218, 215, 0.4) 50%,
		rgba(241, 218, 215, 0) 100%
	);
}

.articlePG__other {
	margin-top: 60px;
	@media (min-width: $desktop) {
		margin-top: 80px;
	}
}

.servicesPG__tabs,
.articlesPG__tabs {
	display: flex;
	justify-content: start;
	gap: 8px;
	margin-bottom: 32px;
}

.servicesPG__tab,
.articlesPG__tab {
	@include HeaderH3;
	border: none;
	background: none;
	opacity: 0.3;
	padding: 0;

	&--khaki {
		color: $Khaki100;
	}

	&--badge {
		color: $brandBeige100;
	}

	&--active {
		opacity: 1;
	}
}
.servicesPG__search {
	margin-bottom: 40px;
}

.articlesPG__search {
	margin-bottom: 32px;
}

.servicesPG__block {
	margin-bottom: 60px;
	display: none;
	@media (min-width: $desktop) {
		display: block;
	}

	&--visibleOnMobile {
		display: block;
	}

	& + & {
		@media (min-width: $desktop) {
			margin-top: 80px;
		}
	}
}

.floatBtn {
	display: block;
	width: calc(100% - 30px) !important;
	text-align: center;
	position: fixed;
	bottom: 40px;
	left: 15px;
	@media (min-width: $desktop) {
		width: fit-content !important;
		left: initial;
		right: 130px;
		bottom: 60px;
		justify-self: initial;
	}
}

.articlesPG__block {
	display: none;
	@media (min-width: $desktop) {
		display: block;
	}

	&--visibleOnMobile {
		display: block;
	}

	& + & {
		@media (min-width: $desktop) {
			margin-top: 80px;
		}
	}
}

.articlesPG__blockHeading,
.servicesPG__blockHeading {
	@include HeaderH1;
	color: $brandBeige100;
	margin: 0 0 60px;
	text-transform: lowercase;

	&--khaki {
		color: $Khaki100;
	}
}

.articlesPG__block {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		left: calc((100vw - 100%) / -2);
		top: 0;
		width: 100vw;
		height: 100%;
		z-index: -1;
		background: linear-gradient(
			180deg,
			rgba(241, 218, 215, 0) 0%,
			rgba(241, 218, 215, 0.4) 50%,
			rgba(241, 218, 215, 0) 100%
		);
	}

	&--khaki {
		&::before {
			background: linear-gradient(
				180deg,
				rgba(236, 243, 231, 0) 0%,
				rgba(236, 243, 231, 0.6) 50%,
				rgba(241, 218, 215, 0) 100%
			);
		}
	}
}

.servicesPG__block {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		left: calc((100vw - 100%) / -2);
		top: 0;
		width: 100vw;
		height: 100%;
		z-index: -1;
		background: linear-gradient(
			180deg,
			rgba(241, 218, 215, 0) 0%,
			rgba(241, 218, 215, 0.4) 50%,
			rgba(241, 218, 215, 0) 100%
		);
	}

	&--khaki {
		&::before {
			background: linear-gradient(
				180deg,
				rgba(236, 243, 231, 0) 0%,
				rgba(236, 243, 231, 0.6) 50%,
				rgba(241, 218, 215, 0) 100%
			);
		}
	}
}
