.frontAbout {
	display: block;
	padding-top: 80px;
	padding-bottom: 60px;
	background: linear-gradient(180deg, #fbe5e1 0%, #ffffff 100%);
	border-top-right-radius: 190px;
	border-top-left-radius: 190px;
	margin-top: 140px;
	position: relative;
	max-width: $containerWidth;
	padding-left: $containerPadding;
	padding-right: $containerPadding;
	margin-left: auto;
	margin-right: auto;

	@media (min-width: $desktop) {
		border-top-right-radius: 640px;
		border-top-left-radius: 640px;
	}

	&::before {
		position: absolute;
		top: -90px;
		left: 0;
		width: 100%;
		height: 180px;
		content: "";
		background-image: url("../assets/about-flower.png");
		background-repeat: no-repeat;
		background-position: center center;
	}

	.blockHeading {
		color: $Monochrome100;
		margin: 20px 0 10px;
		@media (min-width: $desktop) {
			margin: 30px 0 27px;
		}
	}
	.about__labels {
		@media (min-width: $desktop) {
			margin-top: 27px;
		}
	}
}

.frontAbout__text {
	margin: 0 auto;
	@include Regular16;
	color: $Monochrome100;
	text-align: center;

	@media (min-width: $desktop) {
		@include Regular18;
	}

	& + & {
		margin-top: 20px;
	}
}

.frontAbout__labels {
	margin-top: 20px;
	@media (min-width: $desktop) {
		margin-top: 27px;
	}
}

.frontAbout__btn {
	margin-top: 29px;
	background-color: $brandBeige100;
	width: 189px;
	@media (min-width: $desktop) {
		margin-top: 40px;
		width: 240px;
	}
}
