.socials__list {
	display: flex;
	gap: 16px;
	list-style-type: none;
	margin: 0;
	padding: 0;
	justify-content: center;
}

.socials__link {
	width: 56px;
	height: 56px;
	border-radius: 100%;
	background-color: $Khaki100;
	display: flex;
	align-items: center;
	justify-content: center;
}

.socials--small {
	.socials__link {
		width: 40px;
		height: 40px;
	}

	svg {
		width: 16px;
		height: 16px;
	}
}
