.header {
	display: block;
	position: relative;
	padding-top: 20px;
}

.header__container {
	display: grid;
}

.header--short {
	.header__container {
		max-width: 1020px;
	}
}

.header:not(.header--short) {
	.header__upper {
		border-bottom: 1px solid #e8d9d280;
	}
}

.header__upper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-bottom: 16px;
	@media (min-width: $desktop) {
		padding: 0 24px 24px;
	}
}

.header__logo {
	display: flex;
	align-items: center;
}
.header__links {
	display: flex;
	gap: 25px;
	align-items: center;
}

.header__schedule {
	border-radius: 28px;
	background-color: $Khaki100;
	padding: 9px 19px;
	@include CtaRegular14;
	color: $Monochrome00;
	text-decoration: none;
}

.header__button {
	border: none;
	background: none;
	display: flex;
	align-items: center;
	justify-content: center;
}
.header__nav {
	width: 100%;
	overflow: hidden;
	padding-top: 10px;
	margin: 0 auto;
	z-index: 1;
	@media (max-width: $upToDesktop) {
		height: 100vh;
		height: 100dvh;
		overflow-y: auto;
		position: fixed;
		top: 0;
		left: 0;
		background-color: $brandBeige20;
	}
	@media (min-width: $desktop) {
		padding-top: 0;
	}

	&--hiddenOnMobile {
		@media (max-width: $upToDesktop) {
			display: none;
		}
	}
}

.header__socials {
	margin-top: 50px;
	margin-bottom: 80px;
}

.header__list {
	list-style-type: none;
	padding: 0;
	display: flex;
	flex-direction: column;
	gap: 4px;
	margin: 0;
	text-align: center;
	@media (min-width: $desktop) {
		flex-direction: row;
		gap: 20px;
		justify-content: space-between;
		align-items: center;
	}
}
.header__item:first-child {
	@include HeaderH1;
	color: $brandBeige40;
	text-transform: uppercase;
}

.header__item {
	@include HeaderH4;
	text-transform: none;
	padding: 11px;
	@media (min-width: $desktop) {
		@include CtaMedium14;
		padding: 15px 24px;
	}
}

.header__link {
	color: $brandBeige100;
	text-decoration: none;
}

.header__btnWrapper {
	width: 100%;
	height: 130px;
	margin: auto auto 0;
	padding-top: 20px;
	padding-bottom: 50px;
	position: static;
	left: 0;
	bottom: -300px;
	border-radius: 100%;
	background: linear-gradient(
		180deg,
		#ffffff 0%,
		rgba(255, 255, 255, 0.2) 100%
	);
	@media (min-width: $desktop) {
		height: auto;
	}
}
.header__button--close {
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px;
	svg {
		width: 28px;
		height: 28px;
	}
}

.header__callLink {
	display: grid;
	justify-content: center;
	justify-items: center;
	a {
		text-decoration: none;
		color: $Monochrome100;
		@include CtaMedium16;
	}
	span {
		@include CtaRegular12;
		color: $Monochrome60;
	}
}
