.reviewsList {
	display: block;
	margin-top: 60px;
	margin-bottom: 60px;
	@media (min-width: $desktop) {
		margin-bottom: 80px;
	}
}

.reviewsList--slider {
	background: linear-gradient(
		180deg,
		rgba(236, 243, 231, 0) 0%,
		rgba(236, 243, 231, 0.8) 50%,
		rgba(236, 243, 231, 0) 100%
	);
}

.reviewsList__list {
	display: grid;
	gap: 47px;
	padding-top: 32px;
	@media (min-width: $desktop) {
		grid-template-columns: repeat(auto-fit, minmax(580px, 1fr));
		gap: 55px 20px;
	}
}

.reviewsList__item {
	display: grid;
	justify-items: center;
	padding: 40px 24px 32px 24px;
	backdrop-filter: blur(20px);
	background: rgba(255, 255, 255, 0.7);
	box-shadow: 0px 4px 10px 0px #0000000a;
	border-radius: 24px;
	position: relative;
	grid-template-rows: min-content min-content min-content 1fr;
	@media (min-width: $desktop) {
		padding: 40px 40px 32px;
	}
}

.reviewsList__itemAvatar {
	width: 64px;
	height: 64px;
	border-radius: 100%;
	border: 4px solid #f9f9f9;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: -32px;
	left: 50%;
	transform: translateX(-50%);
	@include HeaderH4;
}

.reviewsList__item {
	&:nth-child(4n + 1) .reviewsList__itemAvatar {
		background-color: $Monochrome20;
		color: $Monochrome60;
	}

	&:nth-child(4n + 2) .reviewsList__itemAvatar {
		background-color: $Green40;
		color: $Green100;
	}

	&:nth-child(4n + 3) .reviewsList__itemAvatar {
		background-color: $Red40;
		color: $Red100;
	}

	&:nth-child(4n + 4) .reviewsList__itemAvatar {
		background-color: $Orange40;
		color: $Orange100;
	}
}

.reviewsList__itemName {
	@include CtaMedium14;
	color: $Monochrome100;
	margin-bottom: 4px;
	margin-top: 10px;
	@media (min-width: $desktop) {
		@include CtaMedium16;
		margin-top: 4px;
	}
}

.reviewsList__itemRaiting {
	margin-bottom: 4px;
	display: flex;
	align-items: center;
	@media (min-width: $desktop) {
		margin-bottom: 8px;
	}
	svg {
		flex-shrink: 0;
		width: 30px;
		height: 30px;
	}
}

.reviewsList__itemText p {
	margin: 0;
	@include Regular16;
	color: $Monochrome100;
	text-align: center;
	@media (min-width: $desktop) {
		@include Regular18;
	}
}

.reviewsList--slider {
	@media (max-width: $upToDesktop) {
		.reviewsList__list {
			overflow: hidden;
			margin-left: calc($containerPadding * -1);
			margin-right: calc($containerPadding * -1);
			padding-left: $containerPadding;
			padding-right: $containerPadding;
		}

		.flickity-viewport {
			overflow: visible;
		}
	}

	@media (min-width: $desktop) {
		.container {
			overflow: hidden;
		}

		.flickity-viewport {
			overflow: visible;
		}
	}

	.reviewsList__list {
		display: block;
	}

	.reviewsList__item {
		height: 405px;
		width: 335px;
		margin-right: 10px;
		@media (min-width: $desktop) {
			width: 580px;
			height: 339px;
			margin-right: 20px;
		}
	}
}
