.visually-hidden {
	position: absolute;
	left: 0;
	top: 0;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: none;
	overflow: hidden;
	white-space: nowrap;
	clip: rect(0, 0, 0, 0);
	clip-path: inset(100%);
}

.scrollOff {
	overflow: hidden;
}

.scrollOnMobileOff {
	@media (max-width: ($desktop - 1px)) {
		overflow: hidden;
	}
}

.hidden {
	display: none !important;
}

.onlyMobile {
	@media (min-width: $desktop) {
		display: none !important;
	}
}

.onlyDesktop {
	@media (max-width: ($desktop - 1px)) {
		display: none !important;
	}
}

.container {
	max-width: $containerWidth;
	width: 100%;
	margin: 0 auto;
	padding: 0 $containerPadding;
}

.loading,
