.servicesCategories__categories {
	@media (min-width: $desktop) {
		display: grid;
		gap: 40px;
		grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
	}
}

.servicesCategories__categoryTitle {
	@include HeaderH5;
	color: $brandBeige100;
	text-decoration: none;
	@media (min-width: $desktop) {
		@include HeaderH4;
	}
}
.servicesCategories__category {
	display: grid;
	gap: 16px;
	border-right: 1px solid #e8d9d2;
	@media (min-width: $desktop) {
		gap: 24px;
	}

	& + & {
		margin-top: 24px;
		@media (min-width: $desktop) {
			margin-top: 0;
		}
	}

	&:has(.servicesCategories__categoryServices) {
		padding: 16px 0;
	}
}

.servicesCategories__category--selected {
	background-color: $Khaki40;
	position: relative;
	border: none;
	border-radius: 24px;
	padding: 32px 16px !important;
	&::before {
		position: absolute;
		top: -48px;
		left: 50%;
		transform: translateX(-50%);
		width: 293px;
		height: 77px;
		content: "";
		background-image: url(../assets/operations-upFlower.png);
		background-repeat: no-repeat;
		background-size: 100% 100%;
		@media (min-width: $desktop) {
			top: -55px;
		}
	}
}

.servicesCategories__categoryServices {
	display: grid;
	gap: 16px;
	@media (min-width: $desktop) {
		gap: 24px;
	}
}

.servicesCategories__categoryService {
	@include Regular14;
	color: $Monochrome100;
	text-decoration: none;
	position: relative;
	margin-left: 20px;
	@media (min-width: $desktop) {
		@include Regular18;
	}

	&::before {
		width: 4px;
		height: 4px;
		border-radius: 50%;
		background-color: $Monochrome100;
		top: 9px;
		left: -14px;
		content: "";
		position: absolute;
		transform: translateY(-50%);
		@media (min-width: $desktop) {
			top: 12px;
		}
	}
}

a.servicesCategories__categoryService,
a.servicesCategories__categoryTitle {
	&:hover {
		text-decoration: underline;
	}
}

.servicesPG__block--khaki {
	.servicesPG__blockHeading,
	.servicesCategories__categoryTitle {
		color: $Khaki100;
	}
}
