.labels {
	display: flex;
	justify-content: center;
}

.labels__labels {
	display: flex;
	gap: 10px;

	@media (min-width: $desktop) {
		gap: 20px;
	}
}

.labels__label {
	width: 140px;
	padding: 18px 23px 14px;
	border-radius: 24px;
	background-color: $Khaki40;
	color: $Khaki100;
	display: grid;
	justify-items: center;
	justify-content: center;
	@media (min-width: $desktop) {
		width: 160px;
	}

	p {
		margin: 0;
		@include Regular14;
		text-align: center;
	}

	b {
		@include HeaderH4;
	}
}
