.certsList {
	display: block;
	padding-top: 20px;
	padding-bottom: 60px;
	@media (min-width: $desktop) {
		padding-top: 100px;
		padding-bottom: 100px;
	}
}

.certsList__list {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(167px, 1fr));
	gap: 30px 10px;
	@media (min-width: $desktop) {
		grid-template-columns: repeat(auto-fit, minmax(265px, 1fr));
		gap: 40px;
	}
}

.certsList__itemTitle {
	margin-top: 10px;
	padding: 0 10px;
	@media (min-width: $desktop) {
		margin-top: 20px;
	}
}

.certsList__img {
	display: block;
	border-radius: 8px;
	aspect-ratio: 1280/900;
	object-fit: cover;
}

.certsList__itemTitle {
	@include Regular12;
	color: $Monochrome100;
	-webkit-line-clamp: 2;
	overflow: hidden;
	text-overflow: ellipsis;
	text-align: center;
	-webkit-box-orient: vertical;
	@media (min-width: $desktop) {
		@include Regular14;
	}
}

.certsList__btn {
	margin-top: 20px;
	@media (min-width: $desktop) {
		margin-top: 40px;
	}
}

.certsList--brownHeading {
	.certsList__heading {
		color: $brandBeige100;
	}
}

.certsList--slider {
	@media (max-width: $upToDesktop) {
		overflow: hidden;
		margin-left: calc($containerPadding * -1);
		margin-right: calc($containerPadding * -1);
		padding-left: $containerPadding;
		padding-right: $containerPadding;

		.flickity-viewport {
			overflow: visible;
		}

		.flickity-prev-next-button {
			display: none;
		}

		.certsList__list {
			display: block;
		}

		.certsList__item {
			margin-left: 10px;
			width: calc(100% - 80px);
			display: block;
		}

		.certsList__img {
			display: block;
			width: 100%;
			max-width: 100%;
			aspect-ratio: 300/215;
			object-fit: contain;
			object-position: center center;
			transition: 0.6s;
			max-height: 450px;
		}

		.certsList__item:not(.is-selected) {
			.certsList__img {
				max-height: 150px;
				filter: blur(4px);
			}
		}
	}

	@media (min-width: $desktop) {
		.certsList__item {
			width: calc((100% - 40px) / 3);
			margin-left: 20px;
		}

		.flickity-prev-next-button {
			top: calc(267px / 2 - 20px);
			transform: translateY(0);
		}
	}
}
