.faqList {
	display: block;
	padding-top: 60px;
	padding-bottom: 60px;
	@media (min-width: $desktop) {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

.faqList__heading {
	margin-bottom: 20px;
	@media (min-width: $desktop) {
		margin-bottom: 40px;
	}
}

.faqList__list {
	display: grid;
	gap: 10px;
}
.faqList__item {
	backdrop-filter: blur(20px);
	background-color: rgba(255, 255, 255, 0.7);
	box-shadow: 0px 4px 10px 0px #0000000a;
	border-radius: 24px;
	padding: 24px 64px 24px 24px;
	@media (min-width: $desktop) {
		padding: 24px 96px 24px 32px;
	}
}

.faqList__itemQuestion {
	@include CtaMedium18;
	color: $Monochrome100;
	width: 100%;
	text-align: left;
	border: none;
	padding: 0;
	background: none;
	position: relative;
	@media (min-width: $desktop) {
		@include CtaMedium20;
	}

	&::after {
		background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='40' height='40' rx='8' fill='%23F8F1ED'/%3E%3Cpath d='M20 12V28' stroke='%239F8D8D' stroke-width='2.6' stroke-linecap='square' stroke-linejoin='round'/%3E%3Cpath d='M28 20H12' stroke='%239F8D8D' stroke-width='2.6' stroke-linecap='square' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		background-position: center center;
		background-repeat: no-repeat;
		content: "";
		top: 0;
		right: -46px;
		width: 40px;
		height: 40px;
		position: absolute;
		@media (min-width: $desktop) {
			width: 48px;
			height: 48px;
			top: -8px;
			right: -80px;
		}
	}
}
.faqList__item--active .faqList__itemQuestion::after {
	background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='40' height='40' rx='8' fill='%23F8F1ED'/%3E%3Cpath d='M28 20H12' stroke='%239F8D8D' stroke-width='2.6' stroke-linecap='square' stroke-linejoin='round'/%3E%3C/svg%3E ");
}

.faqList__itemAnswer {
	@include Regular16;
	color: $Monochrome100;
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	transition: max-height 0.5s ease, opacity 0.5s ease;
	margin-top: 12px;
	@media (min-width: $desktop) {
		@include Regular18;
		margin-top: 16px;
	}

	p {
		margin: 0;

		& + p {
			margin-top: 16px;
		}
	}

	a {
		@include Regular16;
		color: $brandBeige100;
		margin-top: 12px;
		@media (min-width: $desktop) {
			@include Regular18;
		}
	}
}
.faqList__item--active .faqList__itemAnswer {
	max-height: 1000px;
	opacity: 1;
	overflow: visible;
	margin-top: 12px;
	@media (min-width: $desktop) {
		margin-top: 16px;
	}
}
