.articlesList__heading {
	margin-bottom: 32px;
}

.articlesList__list {
	display: grid;
	gap: 40px;
	grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
	@media (min-width: $desktop) {
		gap: 40px 20px;
	}
}

.articlesList__item {
	display: grid;
	gap: 12px;
	@media (min-width: $desktop) {
		gap: 16px;
	}
}

.articlesList__itemImg {
	display: block;
	width: 100%;
	aspect-ratio: 380/240;
	object-fit: cover;
	border-radius: 16px;
}

.articlesList__itemTitleLink {
	@include HeaderH5;
	color: $brandBeige100;
	text-decoration: none;
	@media (min-width: $desktop) {
		@include HeaderH4;
	}
}

.articlesList__desc {
	@include Regular14;
	color: $brandBeige100;
	@media (min-width: $desktop) {
		@include Regular16;
	}
}

.articlesList__date {
	@include Regular14;
	color: $brandBeige60;
	@media (min-width: $desktop) {
		@include Regular16;
	}
}

.articlesList__more {
	margin-top: 32px;
	@media (min-width: $desktop) {
		margin-top: 60px;
	}
}

.articlesList--sliderOnMobile {
	@media (max-width: $upToDesktop) {
		.articlesList__list {
			overflow-x: auto;
			margin-left: calc(-1 * $containerPadding);
			margin-right: calc(-1 * $containerPadding);
			padding-left: $containerPadding;
			padding-right: $containerPadding;
			scroll-snap-type: x mandatory;
			display: flex;
			gap: 10px;
		}

		.articlesList__item {
			scroll-snap-align: center;
			flex-shrink: 0;
			width: 330px;
			max-width: calc(100% - 45px);
		}
	}
}

