.about {
	position: relative;
	margin-bottom: 60px;
	@media (min-width: $desktop) {
		margin-bottom: 100px;
	}

	&::before {
		position: absolute;
		width: calc(100% + $containerPadding * 2);
		left: calc($containerPadding * -1);
		height: 200%;
		bottom: 26px;
		content: "";
		display: block;
		background: linear-gradient(180deg, #f5f7fb 0%, #fbe5e1 100%);
		border-bottom-left-radius: 190px;
		border-bottom-right-radius: 190px;
		z-index: -1;
		@media (min-width: $desktop) {
			height: 780px;
			left: 0;
			top: initial;
			border-bottom-left-radius: 640px;
			border-bottom-right-radius: 640px;
		}
	}
}

.about__container {
	display: grid;
	justify-items: center;
}

.about__heading {
	@include HeaderH4;
	margin-bottom: 10px;
	text-align: center;
	margin-top: -35px;
	@media (min-width: $desktop) {
		@include HeaderH3;
		margin-bottom: 16px;
	}
}

.about__desc {
	@include Regular16;
	text-align: center;
	max-width: 528px;
	@media (min-width: $desktop) {
		@include Regular18;
	}
}

.about__btn {
	background-color: $brandBeige100;
	width: 200px;
	margin-top: 20px;
	@media (min-width: $desktop) {
		width: 240px;
		margin-top: 32px;
	}
}

.about__labels {
	margin-top: 32px;
}

.about--dark {
	.about__heading {
		color: $Monochrome100;
	}

	.about__desc {
		color: $Monochrome100;
	}
}

.about--bigCircle {
	padding-bottom: 65px;
	@media (min-width: $desktop) {
		padding-bottom: 60px;
	}

	&::before {
		bottom: 0;
	}
}
