.contacts__block {
	text-align: center;

	& + & {
		margin-top: 48px;
	}
}

.contacts__blockContact {
	@include HeaderH5;
	color: $brandBeige100;
	text-decoration: none;
	@media (min-width: $desktop) {
		@include HeaderH4;
	}

	&:hover {
		text-decoration: underline;
	}
}

.contacts__blockContactNote {
	@include Regular14;
	color: $brandBeige100;
	margin-top: 8px;
	@media (min-width: $desktop) {
		@include Regular12;
	}
}

.contacts--smallGap {
	.contacts__block + .contacts__block {
		margin-top: 16px;
	}

	.contacts__blockContactNote {
		margin-top: 4px;
	}
}

.contacts--black {
	.contacts__blockContact {
		color: $Monochrome100;
	}

	.contacts__blockContactNote {
		color: $Monochrome80;
	}
}
