.footer {
	padding-bottom: 50px;
	margin-top: 60px;
	@media (min-width: $desktop) {
		margin-top: 80px;
	}
}

.footer__dev {
	@include Regular12;
	color: $Monochrome80;
	text-align: center;
	margin: 16px auto;
	text-decoration: dotted;
	text-align: center;
	display: block;
	width: fit-content;

	&:hover {
		text-decoration: underline;
	}
}

.footer__copyright {
	@include Regular12;
	color: $Monochrome40;
	display: grid;
	justify-content: center;
	@media (min-width: $desktop) {
		display: flex;
		gap: 5px;
	}

	a {
		color: inherit;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}
