.servicesList {
	display: block;
	text-align: center;
	background: linear-gradient(
		180deg,
		rgba(241, 218, 215, 0) 0%,
		rgba(241, 218, 215, 0.4) 50%,
		rgba(241, 218, 215, 0) 100%
	);
}

.servicesList__heading {
	color: $Monochrome40;
}
.servicesList__list {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10px;
	@media (min-width: $desktop) {
		grid-template-columns: repeat(auto-fit, minmax(288px, 1fr));
	}
}

.servicesList__item {
	padding: 13px 28px;
	@include CtaRegular14;
	color: $Monochrome100;
	position: relative;
	text-decoration: none;
	display: flex;
	align-items: center;
	justify-content: start;
	backdrop-filter: blur(20px);
	box-shadow: 0px 4px 10px 0px #0000000a;
	border-radius: 24px;
	background-color: #ffffffb2;
	@media (min-width: $desktop) {
		padding: 18px 44px;
		@include CtaRegular18;
	}
	&::after {
		background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.33301 13.3334L11.6663 10L8.33301 6.66669' stroke='%23E8D9D2' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		content: "";
		width: 20px;
		height: 20px;
		right: 8px;
		top: 50%;
		transform: translateY(-50%);
		position: absolute;
		@media (min-width: $desktop) {
			width: 24px;
			height: 24px;
			background-size: cover;
		}
	}
}

.servicesList__btn {
	background-color: $Khaki100;
	margin-top: 20px;
	@media (min-width: $desktop) {
		margin-top: 60px;
	}
}
