.btn {
	width: fit-content;
	text-align: center;
	margin: 0 auto;
	border-radius: 28px;
	min-height: 56px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: $Monochrome00;
	text-decoration: none;
	@include CtaRegular18;
	padding: 17px 34px;
	border: none;

	&--fullWidth {
		width: 100%;
		@media (min-width: $desktop) {
			width: fit-content;
			min-width: 280px;
		}
	}

	&--right {
		margin-left: auto;
		margin-right: 0;
	}

	&--beige {
		background-color: $brandBeige100;
	}

	&--khaki {
		background-color: $Khaki100;
	}
}
