.article {
	background: linear-gradient(
		180deg,
		rgba(241, 218, 215, 0) 0%,
		rgba(241, 218, 215, 0.4) 50%,
		rgba(241, 218, 215, 0) 100%
	);
}

.article__back {
	@include CtaMedium18;
	color: $Khaki100;
	text-align: center;
	width: fit-content;
	margin: 0 auto;
	display: block;

	&:hover {
		text-decoration: none;
	}
}

.article__title {
	@include HeaderH4;
	color: $brandBeige100;
	text-align: center;
	margin: 16px auto;
	@media (min-width: $desktop) {
		@include HeaderH2;
		margin: 16px auto 24px;
	}
}

.article__date {
	color: $brandBeige100;
	@include Regular18;
	text-align: center;
}

.article__img {
	margin: 16px auto 40px;
	border-radius: 16px;
	display: block;
	width: 100%;
	max-width: 1180px;
	height: auto;
	border: none;
	overflow: hidden;
	@media (min-width: $desktop) {
		margin: 24px auto 60px;
	}
}

.article__text {
	margin-top: 40px;
}

.article__img + .article__text {
	margin-top: 0;
}

.article__text {
	p,
	li {
		color: $Monochrome100;
		@include Regular18;
		margin: 0;
	}

	p,
	ul,
	ol {
		margin-top: 20px;
	}

	ul,
	ol {
		display: grid;
		gap: 10px;
		@media (min-width: $desktop) {
			gap: 20px;
		}
	}

	ul {
		padding-left: 20px;
		list-style: none;
		@media (min-width: $desktop) {
			border-left: 1px solid #e8d9d2;
			padding-left: 15px;
		}
	}

	h2 {
		@include HeaderH4;
		color: $brandBeige100;
	}

	p,
	ul {
		& + h2 {
			margin-top: 60px;
			@media (min-width: $desktop) {
				margin-top: 100px;
			}
		}
	}

	b,
	strong {
		color: $brandBeige100;
		font-style: normal;
		@include Regular18;
	}

	a {
		@include Regular18;
		color: $brandBeige100;

		&:hover {
			text-decoration: none;
		}
	}
}

.article__btn {
	margin-top: 40px;
	@media (min-width: $desktop) {
		margin-top: 60px;
	}
}
