.search {
	display: block;
	width: 100%;
	position: relative;
}
.search__input {
	@include CtaRegular18;
	color: $Monochrome100;
	border: 1px solid $Monochrome40;
	padding: 16px 24px 16px 56px;
	border-radius: 28px;
	background-color: transparent;
	width: 100%;
	text-align: center;
	text-align: left;

	&:focus {
		border-color: $Khaki100;
		outline: none;
	}
}

.search__submit {
	border: none;
	background: none;
	position: absolute;
	left: 24px;
	top: 50%;
	background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='11.0586' cy='11.0586' r='7.06194' stroke='%23808384' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M20.0033 20.0033L16.0516 16.0517' stroke='%23808384' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	width: 24px;
	height: 24px;
	transform: translateY(-50%);
}
